import {
  Flex,
  HStack,
  Spacer,
  VStack,
  Wrap,
} from "@react-native-material/core";
import { Button, Text, useTheme } from "react-native-paper";
import { Image } from "expo-image";
import { ScrollView, useWindowDimensions } from "react-native";
import Animated, {
  useSharedValue,
  useAnimatedStyle,
} from "react-native-reanimated";
import Container from "./Shared/Container";
import * as Linking from "expo-linking";
import { useNavigation } from "@react-navigation/native";
import { useEffect, useState } from "react";

export default function StartingPage() {
  const theme = useTheme();
  const navigation = useNavigation();
  const { width, height } = useWindowDimensions();
  // const [scrollOffset, setScrollOffset] = useState(0);

  // const scrollY = useSharedValue(0);
  // const coverTransform = useAnimatedStyle(() => {
  //   return {
  //     transform: [
  //       {
  //         translateY: scrollOffset, //<= 0 ? scrollOffset : 0, //interpolate(offSet.value, [-500, 0, 500], [-500, 0, 500])
  //       },
  //     ],
  //   };
  // });

  return (
    <ScrollView
    // onScroll={(event) => {
    //   setScrollOffset(event.nativeEvent.contentOffset.y * -0.5);
    // }}
    // scrollEventThrottle={16}
    >
      <Flex
        center
        w={width}
        h={height}
        style={{
          shadowColor: theme.colors.shadow,
          shadowOffset: { width: 0, height: 0 },
          shadowRadius: "10em",
          // position: "absolute",
        }}
      >
        <Animated.View style={[{ width: "100%", height: "100%" }, {}]}>
          <Image
            source={require("../assets/images/background.jpg")}
            contentFit="cover"
            contentPosition="center"
            style={{ width: "100%", height: "100%" }}
          />
        </Animated.View>

        <Flex
          center
          style={{
            position: "absolute",
            width: "100%",
            height: "100%",
            backgroundColor: theme.colors.backdrop,
            zIndex: 5,
          }}
        >
          <VStack center ph={"10vw"} spacing={20}>
            <Image
              source={require("../assets/images/sai.png")}
              contentFit="contain"
              style={{
                minWidth: "20em",
                maxWidth: "50em",
                minHeight: "10em",
                maxHeight: "40em",
                // backgroundColor: theme.colors.primary,
              }}
            />
            <Wrap spacing={10}>
              <Button
                mode="contained"
                onPress={async () => {
                  await Linking.openURL("https://www.github.com/SAI-COLOMOS");
                }}
              >
                Ver GitHub
              </Button>
            </Wrap>
          </VStack>
        </Flex>
      </Flex>

      <Container>
        <VStack spacing={20}>
          <Flex fill>
            <Text variant="displayMedium">
              ¿Por qué seguir haciendo las cosas a la antigua?
            </Text>
          </Flex>

          <HStack>
            <Text variant="bodyLarge">
              En un mundo donde la era digital es dominante... ¿por qué no
              modernizarse? Gracias a a tecnología podemos automatizar procesos
              que pueden llegar a ser repetitivos, además de que también podemos
              delegar tareas para que sean justamente los dispositivos
              electrónicos quienes se encarguen de llevarlas a cabo, siendo, en
              algunos casos, mejor ejecutas que si un humano lo hubiese hecho
            </Text>
          </HStack>
        </VStack>
      </Container>

      <Container>
        <VStack spacing={20}>
          <Flex fill>
            <Text variant="displayMedium">
              Una plataforma para administrarlo todo
            </Text>
          </Flex>

          <HStack>
            <Text variant="bodyLarge">
              Nosotros creemos que hay mejores maneras de hacer las cosas, una
              de ellas es llevar a cabo la administración del servicio social,
              es por ello, que decidimos crear una plataforma la cual pueda
              cumplir con esta tarea y, de esta manera, es que desarrollamos
              tanto una API como una aplicación móvil
            </Text>
          </HStack>
        </VStack>
      </Container>

      <Container style={{ backgroundColor: theme.colors.primaryContainer }}>
        <VStack spacing={20}>
          <Flex fill>
            <Text variant="displayMedium">
              Aplicación y servidor, un match perfecto
            </Text>
          </Flex>

          <VStack spacing={20}>
            <Text variant="bodyLarge">
              Nuestra plataforma se compone de dos pilares fundamentales:
              nuestro servidor, el cual funciona como una API REST; y nuestra
              aplicación, la cual es el medio por el cual los usuarios harán uso
              de la plataforma
            </Text>

            <Wrap spacing={20} wrap="wrap">
              <VStack fill minW={300} spacing={10}>
                <Image
                  source={require("../assets/images/miro-rest.png")}
                  style={{ width: "10em", height: "10em" }}
                />
                <Text variant="titleLarge">MIRO-REST</Text>
                <Text variant="bodyLarge">
                  Es nuestro servidor, el cual consta de propiamente dicho, un
                  servidor, y que además cuenta con una base de datos
                </Text>

                <Spacer pb={10} />

                <HStack justify="end">
                  <Button mode="outlined">Ver detalles técnicos</Button>
                </HStack>
              </VStack>

              <VStack fill minW={300} spacing={10}>
                <Image
                  source={require("../assets/images/app-core.png")}
                  style={{ width: "10em", height: "10em" }}
                />
                <Text variant="titleLarge">APP-CORE</Text>
                <Text variant="bodyLarge">
                  Es la aplicación mediante la cual, los administradores,
                  encargados y prestadores hará uso de la plataforma
                </Text>
                <Spacer pb={10} />
                <HStack justify="end">
                  <Button
                    mode="outlined"
                    onPress={() =>
                      navigation.navigate("Detalles técnicos de la aplicación")
                    }
                  >
                    Ver detalles técnicos
                  </Button>
                </HStack>
              </VStack>
            </Wrap>
          </VStack>
        </VStack>
      </Container>
    </ScrollView>
  );
}
